.form{display:flex;gap:2rem;padding:4rem 2rem 2rem;background-color:#5368df;color:#fff;flex-direction:column;border:rgba(0,0,0,0)}.form__counter{text-transform:uppercase;letter-spacing:.3ch}.form__title{font-weight:400}.form__body{display:flex;flex-direction:column;align-items:baseline;gap:1rem;width:clamp(300px,80%,20rem);align-self:center;margin:0 auto;width:clamp(300px,80%,30rem);margin:0 auto 3rem}.form__input{border:none;padding:1.25rem;border-radius:4px;outline:none;box-shadow:none;min-height:1rem;margin:0 auto;width:100%;box-sizing:border-box}.form__input-wrapper{margin:0 auto;transition:border .3s ease,outline .3s ease;border:0 solid rgba(0,0,0,0);border-radius:5px;width:100%}.form__input-wrapper--error{outline:5px solid #fa5757}.form__input-wrapper--error::after{content:"Whoops! Make sure it's an email :)";font-size:small;background-color:#fa5757;font-style:italic;color:#fff;width:100%;display:flex;box-sizing:border-box;padding-inline-start:1.2rem}.form__input-wrapper--success{outline:5px solid olive}.form__input-wrapper--success::after{content:"🎉We've received your e-mail! 🎉";font-size:small;background-color:olive;font-style:italic;color:#daa520;width:100%;display:flex;box-sizing:border-box;padding-inline-start:1.2rem}.form__input::placeholder{color:#9194a1}.form__button{border:none;background-color:#fa5757;box-sizing:border-box;max-width:unset;width:100%}.form__button:hover{background-color:#f5f5f5;color:#fa5757;outline:2px solid #fa5757}@media(min-width: 1024px){.form{padding:30px;height:300px;background-color:#5368df}.form__body{flex-direction:row;gap:1rem;justify-content:right;width:clamp(20rem,50%,50ch);align-self:center;justify-content:space-evenly;width:50%;align-items:baseline}.form__button{max-width:8rem}.form__error{width:400px}}