@import '../../styles/variables.scss';

.download-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    gap: 10px;
    padding-block: 1rem;
    border-radius: .5rem;
    background-color: white;
    -webkit-box-shadow: 0px 15px 30px -5px hsl(229, 31%, 75%);
    -moz-box-shadow: 0px 15px 30px -5px hsl(229, 31%, 75%);
    box-shadow: 0px 15px 30px -5px hsl(229, 31%, 75%);
    transition: box-shadow 0.5s ease;
    text-align: unset;

    &__button {
        margin: 5px;
    }

    &:hover {
        -webkit-box-shadow: 0px 15px 30px -5px $lightBlue;
        -moz-box-shadow: 0px 15px 30px -5px $lightBlue;
        box-shadow: 0px 15px 30px -5px $lightBlue;
    }

    &__img {
        width: 100px;
        padding-bottom: 1rem;
    }
}
